
.artist-tiles {
    height: auto;
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    padding: 0 20px;
}

.artist-tiles.contain {
    max-width: 1200px;
}

.artist-tile {
    height: auto;
    flex: 0 0 calc(33.33% - 20px);
    margin: 10px;
    /* border: 1px solid rgba(0, 0, 0, 0.05); */
    border-radius: 0 0 3px 3px;
    overflow: hidden;
    transition: 0.3s ease all;
    color: inherit;
    text-decoration: none;
}

@media (max-width: 600px) {
    .artist-tile {
        flex: 0 0 100%;
        margin: 0;
        margin-bottom: 40px;
    }
}

.artist-tile img {
    display: block;
    width: 100%;
}

.artist-info {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: 'Roboto Mono', monospace;
}

.artist-name {
    font-weight: 100;
    line-height: 1.6em;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-size: 1.3em;
    font-family: 'Archivo Black', sans-serif;
}

.artist-info .learn-more {
    text-decoration: none;
    color: inherit;
    opacity: 0.5;
    text-transform: uppercase;
    font-size: 0.7em;
    font-family: 'Archivo Black', sans-serif;
}