@import url(https://fonts.googleapis.com/css2?family=Archivo+Black&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
html, body, #root {
    height: 100%;
    width: 100%;
    background-color: #000000;
    color: #ffffff;
    font-family: 'Open Sans', sans-serif;
    /* font-family: 'Roboto Mono', monospace; */
}

* {
    box-sizing: border-box;
}

.contain {
    width: 100%;
    max-width: 1080px;
    margin: auto;
}
.landing {
    display: flex;
    height: 100%;
    text-align: center;
    padding: 0 40px;
    background-color: #ff6f58;

    background: linear-gradient(270deg, #000000, #000000, #CE1827, #CE1827);
    background-size: 600% 600%;
    
    -webkit-animation: AnimationName 12s ease infinite;
    animation: AnimationName 12s ease infinite;
}
@-webkit-keyframes AnimationName {
    0%{background-position:0% 66%}
    50%{background-position:100% 35%}
    100%{background-position:0% 66%}
}
@keyframes AnimationName {
    0%{background-position:0% 66%}
    50%{background-position:100% 35%}
    100%{background-position:0% 66%}
}

.landing .logo {
    width: 80%;
    max-width: 400px;
    margin-bottom: 40px;
}

.landing .contain {
    max-width: 740px;
    position: relative;
    z-index: 10;
}

.landing .overlay {
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
}

.landing a {
    color: inherit;
}

.landing h2 {
    color: white;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-weight: 900;
    opacity: 0.8;
}

.landing h1, .landing h2 {
    color: white;
    font-weight: 100;
    font-size: 2em;
    line-height: 1.6em;
    font-family: 'Montserrat', sans-serif;
    /* font-family: 'Archivo Black', sans-serif; */
    text-transform: uppercase;
    margin-bottom: 40px;
}

.landing h2 {
    /* font-family: 'Monoton', cursive; */
    font-family: 'Archivo Black', sans-serif;
    display: block;
    font-size: 2.2;
}

.landing .buttons {

}

.landing .buttons a {
    display: inline-block;
    line-height: 1.3;
    border: 3px solid white;
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 20px;
    border-radius: 3px;
    text-decoration: none;
    transition: 0.15s ease all;
    font-family: 'Archivo Black', sans-serif;
    color: #000000;
    text-transform: uppercase;
}

.landing .buttons a:nth-child(1) {
    background-color: white;
    /* color: black; */
}

.landing .buttons a:last-child {
    margin-right: 0;
    margin-bottom: 0;
}

.landing .buttons a:hover {
    background-color: transparent;
    border: 3px solid white;
    color: white;
}

@media (max-width: 600px) {
    .landing {
        text-align: center;
    }
    .landing .logo {
        /* margin-bottom: 20px; */
    }
    .landing h1, .landing h2 {
        font-size: 1.4em;
        /* margin-bottom: 20px; */
    }
    .landing .buttons a {
        padding: 10px;
        /* margin-bottom: 20px; */
    }
}
.listing-page {
    /* height: 100%; */
    background-color: #000000;
    color: white;
}

.listing-page .section-title {
    padding: 80px 40px;
    text-align: center;
}

.listing-page h4 {
    display: flex;
    line-height: 1.6em;
    font-size: 1em;
    max-width: 600px;
    margin: auto;
    margin-bottom: 40px;
}
.listing-page h4 b {
    color: white;
}

.listing-page h4 p {
    flex: 1 0 50%;
    color: rgba(255, 255, 255, 0.7);
}

.listing-page h4 p:nth-child(1) {
    text-align: right;
    padding-right: 20px;
    border-right: 1px solid rgba(255, 255, 255, 0.3);
}
.listing-page h4 p:nth-child(2) {
    padding-left: 20px;
    text-align: left;
}

.listing-page h3 {
    text-align: center;
    font-size: 2em;
    line-height: 1.6em;
    font-family: 'Archivo Black', sans-serif;
}

@media (max-width: 600px) {
    .listing-page h3 {
        font-size: 1.5em;
    }

    .listing-page h4 {
        font-size: 0.6em;
    }
}

.artist-tiles {
    height: auto;
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
    padding: 0 20px;
}

.artist-tiles.contain {
    max-width: 1200px;
}

.artist-tile {
    height: auto;
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(33.33% - 20px);
    margin: 10px;
    /* border: 1px solid rgba(0, 0, 0, 0.05); */
    border-radius: 0 0 3px 3px;
    overflow: hidden;
    transition: 0.3s ease all;
    color: inherit;
    text-decoration: none;
}

@media (max-width: 600px) {
    .artist-tile {
        flex: 0 0 100%;
        margin: 0;
        margin-bottom: 40px;
    }
}

.artist-tile img {
    display: block;
    width: 100%;
}

.artist-info {
    padding: 20px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: 'Roboto Mono', monospace;
}

.artist-name {
    font-weight: 100;
    line-height: 1.6em;
    margin-bottom: 10px;
    text-transform: uppercase;
    font-size: 1.3em;
    font-family: 'Archivo Black', sans-serif;
}

.artist-info .learn-more {
    text-decoration: none;
    color: inherit;
    opacity: 0.5;
    text-transform: uppercase;
    font-size: 0.7em;
    font-family: 'Archivo Black', sans-serif;
}
.profile-page {
    width: 100%;
    background-color: #000000;
    color: #ffffff;
    transition: 0.3s ease all;
}

.profile-page .x {
    position: fixed;
    top: 0;
    right: 0;
    padding: 20px;
    text-decoration: none;
    z-index: 99;
    color: white;
    font-weight: 900;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 0 0 0 6px;
    font-family: "Archivo Black", sans-serif;
    font-weight: 900;
    text-transform: uppercase;
}

.profile-page .contain {
    padding: 40px;
}

.profile-image {
    width: 100%;
    height: 80vh;
    background-size: cover;
    background-position: center top;
    margin-bottom: 40px;
    position: relative;
}

.profile-socials a:last-child {
    margin-right: 0;
}

.profile-name {
    font-size: 2em;
    margin-bottom: 40px;
    text-transform: uppercase;
    font-family: "Archivo Black", sans-serif;
}

.profile-name span {
    font-size: 0.5em;
    display: inline-block;
}

.profile-bio {
    line-height: 2em;
    margin-bottom: 40px;
}

.profile-bio span {
    line-height: 1.7em;
    font-size: 1.5em;
    display: block;
    margin-bottom: 20px;
}

.profile-text p {
    margin-bottom: 1rem;
}

.profile-content {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 80px;
    margin: 0 -10px;
}

.profile-content .item {
    flex: 1 1;
    padding: 10px;
    min-width: 50%;
}

@media (max-width: 600px) {
    .profile-content {
        flex-direction: column;
    }

    .profile-content .item {
        padding: 0;
        margin-bottom: 20px;
    }

    .profile-content .item:last-child {
        margin-bottom: 0;
    }
}

.profile-socials {
    margin-bottom: 80px;
    /* text-align: center; */
    font-family: "Archivo Black", sans-serif;
    text-transform: uppercase;
    /* text-align: center;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom: 40px; */
}

.profile-socials a {
    max-width: 600px;
    margin: auto;
    border-radius: 6px;
    text-decoration: none;
    color: inherit;
    background-color: inherit;
    text-align: center;
    margin-right: 20px;
    font-size: 1.5em;
}

.footer {
    padding: 40px 20px;
    text-align: center;
    font-family: 'Archivo Black', sans-serif;
    text-transform: uppercase;
    background-color: #000000;
    color: white;
}

.footer .copyright {
    margin-bottom: 40px;
}

.socials {
    font-size: 1.3em;
}

.socials a {
    letter-spacing: 10px;
    color: inherit;
    opacity: 0.3;
    transition: 0.3s ease all;
}

.socials a:hover {
    opacity: 1;
}
