.footer {
    padding: 40px 20px;
    text-align: center;
    font-family: 'Archivo Black', sans-serif;
    text-transform: uppercase;
    background-color: #000000;
    color: white;
}

.footer .copyright {
    margin-bottom: 40px;
}

.socials {
    font-size: 1.3em;
}

.socials a {
    letter-spacing: 10px;
    color: inherit;
    opacity: 0.3;
    transition: 0.3s ease all;
}

.socials a:hover {
    opacity: 1;
}