.landing {
    display: flex;
    height: 100%;
    text-align: center;
    padding: 0 40px;
    background-color: #ff6f58;

    background: linear-gradient(270deg, #000000, #000000, #CE1827, #CE1827);
    background-size: 600% 600%;
    
    -webkit-animation: AnimationName 12s ease infinite;
    -moz-animation: AnimationName 12s ease infinite;
    animation: AnimationName 12s ease infinite;
}
@-webkit-keyframes AnimationName {
    0%{background-position:0% 66%}
    50%{background-position:100% 35%}
    100%{background-position:0% 66%}
}
@-moz-keyframes AnimationName {
    0%{background-position:0% 66%}
    50%{background-position:100% 35%}
    100%{background-position:0% 66%}
}
@keyframes AnimationName {
    0%{background-position:0% 66%}
    50%{background-position:100% 35%}
    100%{background-position:0% 66%}
}

.landing .logo {
    width: 80%;
    max-width: 400px;
    margin-bottom: 40px;
}

.landing .contain {
    max-width: 740px;
    position: relative;
    z-index: 10;
}

.landing .overlay {
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
}

.landing a {
    color: inherit;
}

.landing h2 {
    color: white;
    margin-bottom: 20px;
    text-transform: uppercase;
    font-weight: 900;
    opacity: 0.8;
}

.landing h1, .landing h2 {
    color: white;
    font-weight: 100;
    font-size: 2em;
    line-height: 1.6em;
    font-family: 'Montserrat', sans-serif;
    /* font-family: 'Archivo Black', sans-serif; */
    text-transform: uppercase;
    margin-bottom: 40px;
}

.landing h2 {
    /* font-family: 'Monoton', cursive; */
    font-family: 'Archivo Black', sans-serif;
    display: block;
    font-size: 2.2;
}

.landing .buttons {

}

.landing .buttons a {
    display: inline-block;
    line-height: 1.3;
    border: 3px solid white;
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 20px;
    border-radius: 3px;
    text-decoration: none;
    transition: 0.15s ease all;
    font-family: 'Archivo Black', sans-serif;
    color: #000000;
    text-transform: uppercase;
}

.landing .buttons a:nth-child(1) {
    background-color: white;
    /* color: black; */
}

.landing .buttons a:last-child {
    margin-right: 0;
    margin-bottom: 0;
}

.landing .buttons a:hover {
    background-color: transparent;
    border: 3px solid white;
    color: white;
}

@media (max-width: 600px) {
    .landing {
        text-align: center;
    }
    .landing .logo {
        /* margin-bottom: 20px; */
    }
    .landing h1, .landing h2 {
        font-size: 1.4em;
        /* margin-bottom: 20px; */
    }
    .landing .buttons a {
        padding: 10px;
        /* margin-bottom: 20px; */
    }
}