.profile-page {
    width: 100%;
    background-color: #000000;
    color: #ffffff;
    transition: 0.3s ease all;
}

.profile-page .x {
    position: fixed;
    top: 0;
    right: 0;
    padding: 20px;
    text-decoration: none;
    z-index: 99;
    color: white;
    font-weight: 900;
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 0 0 0 6px;
    font-family: "Archivo Black", sans-serif;
    font-weight: 900;
    text-transform: uppercase;
}

.profile-page .contain {
    padding: 40px;
}

.profile-image {
    width: 100%;
    height: 80vh;
    background-size: cover;
    background-position: center top;
    margin-bottom: 40px;
    position: relative;
}

.profile-socials a:last-child {
    margin-right: 0;
}

.profile-name {
    font-size: 2em;
    margin-bottom: 40px;
    text-transform: uppercase;
    font-family: "Archivo Black", sans-serif;
}

.profile-name span {
    font-size: 0.5em;
    display: inline-block;
}

.profile-bio {
    line-height: 2em;
    margin-bottom: 40px;
}

.profile-bio span {
    line-height: 1.7em;
    font-size: 1.5em;
    display: block;
    margin-bottom: 20px;
}

.profile-text p {
    margin-bottom: 1rem;
}

.profile-content {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 80px;
    margin: 0 -10px;
}

.profile-content .item {
    flex: 1;
    padding: 10px;
    min-width: 50%;
}

@media (max-width: 600px) {
    .profile-content {
        flex-direction: column;
    }

    .profile-content .item {
        padding: 0;
        margin-bottom: 20px;
    }

    .profile-content .item:last-child {
        margin-bottom: 0;
    }
}

.profile-socials {
    margin-bottom: 80px;
    /* text-align: center; */
    font-family: "Archivo Black", sans-serif;
    text-transform: uppercase;
    /* text-align: center;
    position: absolute;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    bottom: 40px; */
}

.profile-socials a {
    max-width: 600px;
    margin: auto;
    border-radius: 6px;
    text-decoration: none;
    color: inherit;
    background-color: inherit;
    text-align: center;
    margin-right: 20px;
    font-size: 1.5em;
}
