.listing-page {
    /* height: 100%; */
    background-color: #000000;
    color: white;
}

.listing-page .section-title {
    padding: 80px 40px;
    text-align: center;
}

.listing-page h4 {
    display: flex;
    line-height: 1.6em;
    font-size: 1em;
    max-width: 600px;
    margin: auto;
    margin-bottom: 40px;
}
.listing-page h4 b {
    color: white;
}

.listing-page h4 p {
    flex: 1 0 50%;
    color: rgba(255, 255, 255, 0.7);
}

.listing-page h4 p:nth-child(1) {
    text-align: right;
    padding-right: 20px;
    border-right: 1px solid rgba(255, 255, 255, 0.3);
}
.listing-page h4 p:nth-child(2) {
    padding-left: 20px;
    text-align: left;
}

.listing-page h3 {
    text-align: center;
    font-size: 2em;
    line-height: 1.6em;
    font-family: 'Archivo Black', sans-serif;
}

@media (max-width: 600px) {
    .listing-page h3 {
        font-size: 1.5em;
    }

    .listing-page h4 {
        font-size: 0.6em;
    }
}